body {
  margin: 0;
}

.registration-container {
  width: 100%;
  height: 100vh;
  max-height: 1024px;
  background-color: #000;
  justify-content: center;
  display: flex;
  overflow: hidden;
}

.login-image {
  width: 100%;
  max-width: 1024px;
  height: 100%;
  filter: grayscale();
  background: rgba(211, 211, 211, .5) url("../reg-img.ce01755a.png") 50% / cover no-repeat;
  border-radius: 30px 0 0 30px;
}

.registration-form-container {
  width: 100%;
  max-width: 612px;
  color: #fcfcfc;
  flex-direction: column;
  align-items: center;
  margin: 44px;
  display: flex;
}

.signup {
  color: #fcfcfc;
  letter-spacing: .02em;
  width: 230px;
  white-space: nowrap;
  border-right: 3px solid;
  margin-top: 0;
  margin-bottom: 36px;
  font-family: Roboto;
  font-size: 64px;
  font-weight: 500;
  animation: typing .8s steps(8, end) forwards, blinking 1.5s infinite;
  overflow: hidden;
}

@keyframes typing {
  from {
    width: 0;
  }

  to {
    width: 230px;
  }
}

@keyframes blinking {
  0% {
    border-right-color: rgba(0, 0, 0, 0);
  }

  50% {
    border-right-color: #fff;
  }

  100% {
    border-right-color: rgba(0, 0, 0, 0);
  }
}

.registration-form {
  flex-direction: column;
  display: flex;
}

.registration-label-username {
  letter-spacing: .02em;
  width: 0;
  white-space: nowrap;
  margin-bottom: 10px;
  margin-left: 10px;
  font-family: Roboto;
  font-size: 24px;
  font-weight: 400;
  animation: typing-label .8s steps(9, end) .9s forwards;
  overflow: hidden;
}

@keyframes typing-label {
  from {
    width: 0;
  }

  to {
    width: 125px;
  }
}

.reg-name {
  width: 400px;
  height: 50px;
  text-align: center;
  color: #fff;
  background: #302d2d;
  border: none;
  border-radius: 5px;
  margin-top: 10px;
  font-size: 22px;
}

.reg-name:focus {
  outline-offset: .2em;
  outline: 2px dashed #ad2b89;
}

.registration-label-password {
  letter-spacing: .02em;
  width: 0;
  white-space: nowrap;
  margin-top: 10px;
  margin-bottom: 10px;
  margin-left: 10px;
  font-family: Roboto;
  font-size: 24px;
  font-weight: 400;
  animation: typing-label-password .8s steps(8, end) 1.9s forwards;
  overflow: hidden;
}

@keyframes typing-label-password {
  from {
    width: 0;
  }

  to {
    width: 115px;
  }
}

.registration-label-password-confirm {
  letter-spacing: .02em;
  width: 0;
  white-space: nowrap;
  margin-top: 10px;
  margin-bottom: 10px;
  margin-left: 10px;
  font-family: Roboto;
  font-size: 24px;
  font-weight: 400;
  animation: typing-label-password-confirm 1s steps(16, end) 2.7s forwards;
  overflow: hidden;
}

@keyframes typing-label-password-confirm {
  from {
    width: 0;
  }

  to {
    width: 205px;
  }
}

.reg-password {
  width: 400px;
  height: 50px;
  text-align: center;
  color: #fff;
  background: #302d2d;
  border: none;
  border-radius: 3px;
  margin-top: 10px;
  font-size: 22px;
}

.reg-password:focus {
  outline-offset: .2em;
  outline: 2px dashed #ad2b89;
}

.form-button-signup {
  width: 410px;
  height: 87px;
  color: #c4c4c4;
  letter-spacing: .02em;
  background: none;
  border: 4px solid #bd00ff;
  border-radius: 5px;
  margin-top: 35px;
  font-family: Roboto;
  font-size: 30px;
  transition: background-color .3s, color .3s;
}

.form-button-signup:hover {
  color: #fff;
  background-color: #bd00ff;
}

.form-button-signup:focus {
  color: #fff;
  background-color: #bd00ff;
  outline: none;
}

@media (max-width: 428px) {
  .registration-form-container {
    margin: 0;
  }

  .registration-container {
    flex-direction: column;
    gap: 20px;
  }

  .login-image {
    display: none;
  }

  .registration-label-username {
    margin-top: 25px;
  }

  .reg-name {
    width: 100%;
    max-width: 320px;
  }

  .registration-label-password, .registration-label-password-confirm {
    margin-top: 25px;
  }

  .reg-password {
    width: 100%;
    max-width: 320px;
  }

  .registation-form {
    padding: 0;
  }

  .form-button-signup {
    width: 100%;
    margin-top: 25px;
  }
}

@media (min-width: 428.1px) and (max-width: 1024px) {
  .login-image {
    display: none;
  }

  .registration-container {
    max-height: max-content;
    justify-content: center;
    padding: 0;
  }

  .registration-form-container {
    margin-top: 80px;
  }

  .registration-label-password, .registration-label-password-confirm {
    margin-top: 20px;
  }

  .registration-label-username, .form-button-signup {
    margin-top: 30px;
  }

  .reg-password, .reg-name {
    height: 60px;
    margin-top: 10px;
  }
}

